<!--  -->
<template>
  <div id="child_view" class="child_view">
    <div class="btns" v-show="code == 200">
      <el-button class="play" round>
        <i class="iconfont icon-icon_play_"></i>
        <span>播放全部</span>
      </el-button>
    </div>
    <div class="list_out">
      <music-table :pageNum="pageNum" :songList="songList" v-show="code == 200"></music-table>
      <music-no-data :keyWord="keyWord" v-show="(code != 200) & (code != 0)"></music-no-data>
    </div>
    <pagination :code="code" :currentChange="getMusicList" :pageSize="30" :total="total"></pagination>
  </div>
</template>

<script>
import { HttpManager } from "../../api";
import MusicTable from "../../components/MusicTable.vue";
import MusicNoData from "../../components/MusicNoData.vue";
import Pagination from "../../components/Pagination.vue";
export default {
  name: "SearchSong",
  data () {
    return {
      code: 0,
      keyWord: this.$route.query.key,
      songList: [],
      isPlay: false,
      total: 0,
      pageNum: 1
    };
  },

  components: { MusicNoData, MusicTable, Pagination },

  methods: {
    getMusicList (val) {
      var that = this;
      this.pageNum = val;
      // document.getElementById('child_view').scrollIntoView();//跳到列表头
      HttpManager.getSearchByKeyWord("Music", {
        key: this.keyWord,
        pn: val,
        rn: 30
      })
        .then((res) => {
          console.log(res);
          that.code = parseInt(res.code);
          that.total = parseInt(res.data.total);
          that.songList = res.data.list;
        })
        .catch((error) => {
          console.log(error);
        });
    }
  },

  watch: {
    "$route.query.key": {
      handler (newVal, oldVal) {
        this.keyWord = newVal;
        this.getMusicList(1);
      },
      immediate: true //监听首次绑定值
    }
  },

  computed: {},

  //生命周期 - 创建完成（可以访问当前this实例）
  created () { },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { }
};
</script>
<style lang="scss" scoped>
@import "../../assets/css/font";
.child_view {
  min-height: 100px;
  .btns {
    margin: 20px 0 20px;
    .play {
      width: 130px;
      background: #31c27c;
      color: #fff;
      &:hover {
        color: #000;
      }
    }
  }
  .list_out {
    box-shadow: 0 0 20px 0 rgb(65 67 70 / 8%);
  }
}
</style>



